:root {
  --nav-active: #00acdc;
  --header-color: #fff;
  --nav-color: #000;
  --nav-backgroundcolor: #fff;
}

* {
  box-sizing: border-box;
  margin: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-size: 12px;
}

.site_header {
  background-color: var(--header-color);
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav li {
  text-transform: uppercase;
  white-space: nowrap;
  background-color: var(--nav-backgroundcolor);
  align-items: center;
  margin-right: 20px;
  line-height: 60px;
  display: inline;
  position: relative;
}

.nav li a {
  color: var(--nav-color);
  font-weight: 500;
  text-decoration: none;
}

.nav li:last-child {
  margin-right: 0;
}

.nav li .is-active, .nav li a:hover {
  color: var(--nav-active);
}

.nav li .is-active:after {
  content: "";
  background-color: var(--nav-active);
  border-radius: 8px;
  height: 4px;
  position: absolute;
  top: 34px;
  left: 0;
  right: 0;
}
